import React, { useEffect, useState } from "react";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import MUIDataTable from "mui-datatables";

import { MOONFT_URL } from "../constants";

import DefaultImg from "../Images/default.png";

var axios = require("axios");

const useStyles = makeStyles((theme) => ({
  tableOverflow: {
    overflow: "auto",
  },
}));

export default function Tables(props) {
  const [assets, setAssets] = useState([]);
  const [data, setdata] = useState("data");
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loggedUser = localStorage.getItem("loggedUser");

    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        owner_address: loggedUser,
      }),
    };
    fetch(MOONFT_URL + "api/fetch_assets/owner_address", requestOptions)
      .then((response) => response.json())
      .then((response) => {
        console.log(response);
        setAssets(response.message.assets);
        setLoading(false);
      });
  }, []);

  const classes = useStyles();

  const handleSelect = (e) => {
    console.log(e);
  };

  const options = {
    onRowClick: (e) => {
      console.log(e[1]);
      localStorage.setItem("clickedAsset", e[1]);
      props.history.push("/app/Asset-details");
    },
    filter: true,
    filterType: "dropdown",
    Search: false,
    searchOpen: true,
    selectableRows: false,
    expandableRowsOnClick: true,
  };

  return (
    <>
      <Grid
        container
        spacing={4}
        style={{ marginTop: "200px", marginBottom: "100px" }}
      >
        <Grid
          item
          xs={12}
          lg={10}
          style={{ marginLeft: "auto", marginRight: "auto" }}
        >
          {!loading ? (
            <MUIDataTable
              title="Assets"
              data={
                assets
                  ? assets.map((element) => [
                      <img
                        src={
                          element.image_thumbnail_url != null
                            ? element.image_thumbnail_url
                            : DefaultImg
                        }
                        style={{ height: "50px", width: "50px" }}
                      />,
                      element.token_id != null ? element.token_id : "N/A",
                      element.name != null ? element.name : "N/A",
                      element.description != null ? element.description : "N/A",
                    ])
                  : null
              }
              columns={["", "TOKEN ID", "NAME", "DESCRIPTION"]}
              options={options}
            />
          ) : null}
        </Grid>
      </Grid>
    </>
  );
}
