import React, { useState, useEffect } from "react";
import Hot1 from "../Images/3d/yellowcar.obj.glb";
import Hot2 from "../Images/3d/bike.obj.glb";
import Hot3 from "../Images/3d/Astronaut.glb";
import Hot4 from "../Images/3d/Tree.obj.glb";
import ethlogo from "./ethlogo.png";
import { useHistory } from "react-router";

const HotCollections = (props) => {
  
 const [trendingAssets, setTrendingAssets] = useState([]);
 const history = useHistory();

 const listdata = [
     {id:'1', img: Hot1, name:"3D Car Model", description:"Set of different design cars", priceTitle:"ETH", price:"0.0500", "locked": true },
     {id:'2', img: Hot2, name:"3D Bike", description:"Set of different design bikes", priceTitle:"ETH", price:"0.0500", "locked": false },
     {id:'3', img: Hot3, name:"3D Astronautes", description:"Set of our smart astronautes", priceTitle:"ETH", price:"0.0500", "locked": true },
     {id:'4', img: Hot4, name:"3D Tree", description:"Set of our christmas trees", priceTitle:"ETH", price:"0.0500", "locked": false },
];

useEffect(() => {
  setTrendingAssets(listdata);
}, []);

  const handleClick = (e) => {
    localStorage.setItem("clickedProduct", JSON.stringify(e));
    history.push("/ProductDetailsTd");
  };

return (
  <div className="container-fluid">
    <div className="row">
      <div className="col-sm-12">
        <h5 style={{textAlign: "left", marginLeft: "2%"}}>HOT COLLECTIONS</h5>
      </div>
    </div>
    <div className="row">

    {trendingAssets
        .slice(0, 20)
        .filter(function (element) {
          return element.price !== null;
        })
        .map((element) => 
      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-3">

          <div onClick={() => handleClick(element)}>
            <div className="scroller--content" style={{paddingTop: "0"}}>
      
              <i class="fa fa-lock" aria-hidden="true" style={{float: "left", marginLeft: "10px", marginTop: "10px"}}></i>
              <model-viewer src={element.img} style={{width: "100%", height: "320px"}} alt="A 3D model" ar ar-modes="webxr scene-viewer quick-look" environment-image="neutral" auto-rotate camera-controls></model-viewer>
             
              <p>{element.name}<br/> <img src={ethlogo} style={{width: "10px", height: "10px"}} />{element.price}</p>
            </div>
          </div>
      </div>
       )}
    </div>
  </div>
);
};


export default HotCollections;
