import React, { useState, useEffect } from "react";
import Art1 from "../Images/art1.png";
import Art2 from "../Images/art2.png";
import Art3 from "../Images/art3.png";
import Art4 from "../Images/art4.png";
import ethlogo from "./ethlogo.png";
import { useHistory } from "react-router";

const DigitalArt = (props) => {
  const [trendingAssets, setTrendingAssets] = useState([]); 
  const history = useHistory();

   const listdata = [
       {id:'1', img: Art1, name:"Shape Game", description:"Set of mathematical equipments", priceTitle:"ETH", price:"0.0512", "locked": false},
       {id:'2', img: Art2, name:"Smiling Face", description:"Faces created on plastic materials", priceTitle:"ETH", price:"0.12", "locked": false },
       {id:'3', img: Art3, name:"Pikkachu", description:"Lorem ipsum dolor sit amet", priceTitle:"ETH", price:"0.0500", "locked": false },
       {id:'4', img: Art4, name:"Metallic Robo", description:"Lorem ipsum dolor sit amet", priceTitle:"ETH", price:"0.0500", "locked": false },
  ];

  useEffect(() => {
    setTrendingAssets(listdata);
  }, []);

    const handleClick = (e) => {
      localStorage.setItem("clickedProduct", JSON.stringify(e));
      history.push("/ProductDetails");
    };

  return (
    <div className="container-fluid">
      <div className="row">
        <div className="col-sm-12">
          <h5 style={{textAlign: "left", marginLeft: "2%"}}>DIGITAL COLLECTIONS</h5>
        </div>
      </div>
      <div className="row">

      {trendingAssets
          .slice(0, 20)
          .filter(function (element) {
            return element.price !== null;
          })
          .map((element) => 
        <div className="col-xs-12 col-sm-6 col-md-6 col-lg-3">
            <div onClick={() => handleClick(element)}>
              <div className="scroller--content">
                <div>
                  <i className="fa fa-heart-o heart-icn" />
                </div>
                <img src={element.img} />
                <p>{element.name}<br/> <img src={ethlogo} style={{width: "10px", height: "10px"}} />{element.price}</p>
              </div>
            </div>
        </div>
         )}
      </div>
    </div>
  );
};

export default DigitalArt;
