import React, { useEffect, useState } from "react";
import {
  Grid,
  CircularProgress,
  Typography,
  Button,
  Tabs,
  Tab,
  TextField,
  Fade,
  Row,
} from "@material-ui/core";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import useStyles from "../Components/style";
import swal from "sweetalert";

var request = require("request");

const HDWalletProvider = require("truffle-hdwallet-provider");
const web3 = require("web3");
var axios = require("axios");

const MNEMONIC = "";
const NODE_API_KEY = "";
const isInfura = false;
const FACTORY_CONTRACT_ADDRESS = "";
const NFT_CONTRACT_ADDRESS = "";
const OWNER_ADDRESS = "";
const NETWORK = "mainnet";

const NUM_CREATURES = 1;
const NUM_LOOTBOXES = 4;
const DEFAULT_OPTION_ID = 0;
const LOOTBOX_OPTION_ID = 2;

const NFT_ABI = [
  {
    constant: false,
    inputs: [
      {
        name: "_to",
        type: "address",
      },
    ],
    name: "mintTo",
    outputs: [],
    payable: false,
    stateMutability: "nonpayable",
    type: "function",
  },
];

const AddItem = (props) => {
  var classes = useStyles();
  var [user, setUser] = useState("");
  var [isLoading, setIsLoading] = useState(false);
  var [helperText, setHelperText] = useState("");
  var [errorText, setErrorText] = useState(false);
  var [error, setError] = useState(null);
  var [activeTabId, setActiveTabId] = useState(0);

  var [assetName, setAssetName] = useState("");
  var [description, setDescription] = useState("");
  var [externalURL, setExternalURL] = useState("");
  var [animationURL, setAnimationURL] = useState("");
  var [youtubeURL, setYoutubeURL] = useState("");
  var [category, setCategory] = useState("");
  var [imageData, setImageData] = useState(null);
  var [imageDataObject, setImageDataObject] = useState(null);
  var [imagePreviewData, setImagePreviewData] = useState(null);
  var [imagePreviewDataObject, setImagePreviewDataObject] = useState(null);
  var [imageThumbnailData, setImageThumbnailData] = useState(null);
  var [imageThumbnailDataObject, setImageThumbnailDataObject] = useState(null);
  var [imageOriginalData, setImageOriginalData] = useState(null);
  var [imageOriginalDataObject, setImageOriginalDataObject] = useState(null);
  var [traitType, setTraitType] = useState("");
  var [traitValue, setTraitValue] = useState("");
  var [displayType, setDisplayType] = useState("");
  var [traitsArr, setTraitsArr] = useState([]);

  async function main() {
    const network =
      NETWORK === "mainnet" || NETWORK === "live" ? "mainnet" : "rinkeby";
    const provider = new HDWalletProvider(
      MNEMONIC,
      isInfura
        ? "https://" + network + ".infura.io/v3/" + NODE_API_KEY
        : "https://eth-" + network + ".alchemyapi.io/v2/" + NODE_API_KEY
    );
    const web3Instance = new web3(provider);

    if (NFT_CONTRACT_ADDRESS) {
      const nftContract = new web3Instance.eth.Contract(
        NFT_ABI,
        NFT_CONTRACT_ADDRESS,
        { gasLimit: "1000000" }
      );
      console.log(nftContract);

      // Creatures issued directly to the owner.
      for (var i = 0; i < NUM_CREATURES; i++) {
        const result = await nftContract.methods
          .mintTo(OWNER_ADDRESS)
          .send({ from: OWNER_ADDRESS });

        console.log("Minted creature. Transaction: " + result.transactionHash);
      }
    } else {
      console.error(
        "Add NFT_CONTRACT_ADDRESS or FACTORY_CONTRACT_ADDRESS to the environment variables"
      );
    }
  }

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.target);
    const trait = data.get("traittype");
    setTraitType(trait);
    const traitValue1 = data.get("value");
    setTraitValue(traitValue1);

    console.log(data.get("displaytype"));

    {
      data.get("displaytype") === "text"
        ? traitsArr.push({
            trait_type: data.get("traittype"),
            value: data.get("value"),
          })
        : traitsArr.push({
            trait_type: data.get("traittype"),
            value: data.get("value"),
            display_type: data.get("displaytype"),
          });
    }
    setTraitType("");
    setTraitValue("");
    console.log(traitsArr);
  };

  const handleChange = (event) => {
    setCategory(event.target.value);
  };

  const submitHandler = () => {
    console.log(JSON.stringify(JSON.stringify(traitsArr)));
    console.log(category);
    setIsLoading(true);
    const walletAddress = localStorage.getItem("loggedUser");
    var FormData = require("form-data");
    var fs = require("fs");
    var data = new FormData();
    data.append("assets_name", assetName);
    data.append("description", description);
    data.append("image_data", imageData);
    data.append("image_preview_data", imagePreviewData);
    data.append("image_thumbnail_data", imageThumbnailData);
    data.append("image_original_data", imageOriginalData);
    data.append("external_url", externalURL);
    data.append("animation_url", animationURL);
    data.append("youtube_url", youtubeURL);
    data.append("traits", JSON.stringify(traitsArr));
    data.append("wallet_address", walletAddress);
    data.append("category", category);
    for (var pair of data.entries()) {
      console.log(pair[0] + ", " + pair[1]);
    }
    var config = {
      method: "post",
      url: "http://3.227.99.109/api/add_assets",
      headers: {
        "Content-Type": `multipart/form-data; boundary=${data._boundary}`,
      },
      data: data,
    };

    axios(config)
      .then(function (response) {
        setIsLoading(false);
        console.log(response.data);
        if (response.data.message === "success") {
          console.log("success");
          main();
          swal({
            title: "Item Added Successfully!",
            icon: "success",
            button: "ok",
          });
          setAssetName("");
          setDescription("");
          setAnimationURL("");
          setYoutubeURL("");
          setExternalURL("");
          setCategory("");
          setDisplayType("");
          setImageData(null);
          setImagePreviewData(null);
          setImageThumbnailData(null);
          setImageOriginalData(null);
          setImageDataObject(null);
          setImagePreviewDataObject(null);
          setImageThumbnailDataObject(null);
          setImageOriginalDataObject(null);
          setTraitsArr([]);
        } else {
          swal({
            title: "Something went wrong, make sure asset name is unique",
            icon: "error",
            button: "ok",
          });
        }
      })
      .catch(function (error) {
        setIsLoading(false);

        console.log(error);
      });
  };

  return (
    <div style={{ marginTop: "130px", marginBottom: "130px" }}>
      <h1 style={{ color: "black" }}>Add Item</h1>

      <Grid
        container
        spacing={3}
        xs={12}
        md={8}
        lg={8}
        style={{ marginLeft: "auto", marginRight: "auto" }}
      >
        <Grid item xs={12} md={6} lg={6}>
          <TextField
            value={assetName}
            InputProps={{
              classes: {
                input: classes.textField,
              },
            }}
            onChange={(e) => setAssetName(e.target.value)}
            margin="normal"
            type="text"
            fullWidth
            id="standard-basic"
            label="Asset Name"
            variant="outlined"
          />
        </Grid>

        <Grid item xs={12} md={6} lg={6}>
          <TextField
            id="name"
            InputProps={{
              classes: {
                input: classes.textField,
              },
            }}
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            margin="normal"
            type="text"
            fullWidth
            label="Description"
            multiline
            variant="outlined"
          />
        </Grid>
        <Grid item xs={12} lg={6}>
          <TextField
            id="email"
            InputProps={{
              classes: {
                input: classes.textField,
              },
            }}
            value={externalURL}
            onChange={(e) => setExternalURL(e.target.value)}
            margin="normal"
            type="email"
            fullWidth
            label="External URL"
            variant="outlined"
          />
        </Grid>
        <Grid item xs={12} lg={6}>
          <TextField
            id="password"
            InputProps={{
              classes: {
                input: classes.textField,
              },
            }}
            value={animationURL}
            onChange={(e) => setAnimationURL(e.target.value)}
            margin="normal"
            type="text"
            fullWidth
            minLength={6}
            label="Animation URL"
            variant="outlined"
          />
        </Grid>
        <Grid item xs={12} lg={6}>
          <TextField
            id="password"
            InputProps={{
              classes: {
                input: classes.textField,
              },
            }}
            value={youtubeURL}
            onChange={(e) => setYoutubeURL(e.target.value)}
            margin="normal"
            type="text"
            fullWidth
            minLength={6}
            label="Youtube URL"
            variant="outlined"
          />
        </Grid>
        <Grid item xs={12} lg={6}>
          <FormControl required className={classes.formControl}>
            <InputLabel
              id="demo-simple-select-required-label"
              style={{ marginLeft: "20px" }}
            >
              Category
            </InputLabel>
            <Select
              labelId="demo-simple-select-required-label"
              id="demo-simple-select-required"
              value={category}
              onChange={handleChange}
              className={classes.selectEmpty}
              variant="outlined"
            >
              <MenuItem value="">
                <em>None</em>
              </MenuItem>
              <MenuItem value="ARTS">ARTS</MenuItem>
              <MenuItem value="DOMAIN NAMES">DOMAIN NAMES</MenuItem>
              <MenuItem value="VIRTUAL WORLDS">VIRTUAL WORLDS</MenuItem>
              <MenuItem value="TRADING CARDS">TRADING CARDS</MenuItem>
              <MenuItem value="COLLECTIBLES">COLLECTIBLES</MenuItem>
              <MenuItem value="SPORTS">SPORTS</MenuItem>
              <MenuItem value="UTILITY">UTILITY</MenuItem>
              <MenuItem value="MUSIC AND SONGS">MUSIC AND SONGS</MenuItem>
            </Select>
          </FormControl>
        </Grid>

        <div className="traits">
          <Grid item xs={12} md={6} lg={12}>
            <form onSubmit={handleSubmit} className="traits-form">
              <label>Trait Type:</label>
              <input
                id="traittype"
                placeholder="Trait Type"
                name="traittype"
                type="text"
                required
                value={traitType}
                onChange={(e) => setTraitType(e.target.value)}
              />
              <label>Value:</label>
              <input
                id="value"
                placeholder="value"
                name="value"
                type="text"
                required
                value={traitValue}
                onChange={(e) => setTraitValue(e.target.value)}
              />
              <label> Display Type:</label>
              <select
                value={displayType}
                onChange={(e) => setDisplayType(e.target.value)}
                id="displaytype"
                name="displaytype"
              >
                <option value="text">text</option>
                <option value="boost_number">boost number</option>
                <option value="boost_percentage">boost percentage</option>
              </select>
              <button>Add Trait</button>
            </form>
          </Grid>

          <Grid item xs={12} lg={12}>
            {traitsArr && traitsArr.length > 0 ? (
              <table>
                <thead>
                  <tr>
                    <th>S.No</th>
                    <th>Trait Type</th>
                    <th>Value</th>
                    <th>Display Type</th>
                  </tr>
                </thead>
                <tbody>
                  {traitsArr
                    ? traitsArr.map((e, index) => (
                        <tr>
                          <td>{index + 1}</td>
                          <td>{e.trait_type}</td>
                          <td>{e.value}</td>
                          <td>{e.display_type ? e.display_type : "text"}</td>
                        </tr>
                      ))
                    : null}
                </tbody>
              </table>
            ) : null}
          </Grid>
        </div>

        <Grid item xs={12} lg={6}>
          {imageDataObject ? (
            <img
              style={{ width: "80px", height: "80px", padding: "10px" }}
              src={imageDataObject}
            />
          ) : null}
          <br />
          <Button
            variant="outlined"
            component="label"
            style={{ width: "300px" }}
          >
            Upload Image
            <TextField
              type="file"
              onChange={(e) => (
                setImageData(e.target.files[0]),
                setImageDataObject(URL.createObjectURL(e.target.files[0]))
              )}
              hidden
            />
          </Button>
        </Grid>

        <Grid item xs={12} lg={6}>
          {imagePreviewDataObject ? (
            <img
              style={{ width: "80px", height: "80px", padding: "10px" }}
              src={imagePreviewDataObject}
            />
          ) : null}
          <br />
          <Button
            variant="outlined"
            component="label"
            style={{ width: "300px" }}
          >
            Upload Preview Image
            <input
              type="file"
              onChange={(e) => (
                setImagePreviewData(e.target.files[0]),
                setImagePreviewDataObject(
                  URL.createObjectURL(e.target.files[0])
                )
              )}
              hidden
            />
          </Button>
        </Grid>

        <Grid item xs={12} lg={6}>
          {imageThumbnailDataObject ? (
            <img
              style={{ width: "80px", height: "80px", padding: "10px" }}
              src={imageThumbnailDataObject}
            />
          ) : null}
          <br />
          <Button
            variant="outlined"
            component="label"
            style={{ width: "300px" }}
          >
            Upload Thumbnail Image
            <input
              type="file"
              onChange={(e) => (
                setImageThumbnailData(e.target.files[0]),
                setImageThumbnailDataObject(
                  URL.createObjectURL(e.target.files[0])
                )
              )}
              hidden
            />
          </Button>
        </Grid>

        <Grid item xs={12} lg={6}>
          {imageOriginalDataObject ? (
            <img
              style={{ width: "80px", height: "80px", padding: "10px" }}
              src={imageOriginalDataObject}
            />
          ) : null}
          <br />
          <Button
            variant="outlined"
            component="label"
            style={{ width: "300px" }}
          >
            Upload Original Image
            <input
              type="file"
              onChange={(e) => (
                setImageOriginalData(e.target.files[0]),
                setImageOriginalDataObject(
                  URL.createObjectURL(e.target.files[0])
                )
              )}
              hidden
            />
          </Button>
        </Grid>

        <div className={classes.creatingButtonContainer}>
          {isLoading ? (
            <CircularProgress size={26} />
          ) : (
            <Button
              onClick={submitHandler}
              // disabled={
              //   assetName.length === 0 ||
              //   description.length === 0 ||
              //   externalURL.length === 0 ||
              //   animationURL.length === 0 ||
              //   youtubeURL.length === 0
              // }
              size="large"
              variant="contained"
              fullWidth
              className={classes.createAccountButton}
              style={{ width: "300px", marginTop: "100px" }}
            >
              ADD ITEM
            </Button>
          )}
        </div>
      </Grid>
    </div>
  );
};
export default AddItem;
