import React, { useState, useEffect } from "react";
import { Route , withRouter} from 'react-router-dom';
import Trending0 from "../Images/ethercard.jpg";
import Trending1 from "../Images/ManicTribal.png";
import Trending2 from "../Images/Trending2.png";
import Trending3 from "../Images/Trending3.png";
import ethlogo from "./ethlogo.png";
import { useHistory } from "react-router";

const TrendingCollections = (props) => {
  
 const [trendingAssets, setTrendingAssets] = useState([]);
 const history = useHistory();

 const listdata = [
     {id:'1', img: Trending0, name:"Ether Guardian", description:"Ether Cards Founder Series First Edition Card", priceUsd:"ETH", price:"0.24", "locked": false},
     {id:'2', img: Trending1, name:"Manic Tribal", description:"It’s an abstract piece that just started with a line and turned into that. It turned into a tribal vibes.", priceUsd:"ETH", price:"0.105", "locked": false },
     {id:'3', img: Trending2, name:"Red Soldier", description:"Set of players in video game", priceUsd:"ETH", price:"0.05", "locked": false },
     {id:'4', img: Trending3, name:"Starring Kong", description:"Many faces and actions of kong", priceUsd:"ETH", price:"0.0112", "locked": false },
];

useEffect(() => {
  setTrendingAssets(listdata);
}, []);

  const handleClick = (e) => {
    localStorage.setItem("clickedProduct", JSON.stringify(e));
    history.push("/ProductDetails");
  };

return (
  <div className="container-fluid">
    <div className="row">
      <div className="col-sm-12">
        <h5 style={{textAlign: "left", marginLeft: "2%"}}>TRENDING COLLECTIONS</h5>
      </div>
    </div>
    <div className="row">

    {trendingAssets
        .slice(0, 20)
        .filter(function (element) {
          return element.price !== null;
        })
        .map((element) => 
      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-3">
          <div onClick={() => handleClick(element)}>
            <div className="scroller--content">
              <div>
                <i className="fa fa-heart-o heart-icn" />
              </div>
              <img src={element.img} />
              <p>{element.name}<br/> <img src={ethlogo} style={{width: "10px", height: "10px"}} />{element.price}</p>
            </div>
          </div>
      </div>
       )}
    </div>
  </div>
);
};
export default withRouter(TrendingCollections);
