import React, { useState } from "react";
import { Button, Container, Row, Col, Modal } from "react-bootstrap";
import ModalBg from "../Images/modalimg.png";
import {
  Grid,
  CircularProgress,
  Typography,
  Tabs,
  Tab,
  TextField,
  Fade,
} from "@material-ui/core";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import useStyles from "./style";
import swal from "sweetalert";

var axios = require("axios");

function MydModalWithGrid(props) {
  var classes = useStyles();

  var [isLoading, setIsLoading] = useState(false);
  var [helperText, setHelperText] = useState("");
  var [errorText, setErrorText] = useState(false);
  var [error, setError] = useState(null);
  var [activeTabId, setActiveTabId] = useState(0);
  var [nameValue, setNameValue] = useState("");
  var [loginValue, setLoginValue] = useState("");
  var [passwordValue, setPasswordValue] = useState("");
  var [regFullName, setRegFullName] = useState("");
  var [regUserName, setRegUserName] = useState("");
  var [regEmail, setRegEmail] = useState("");
  var [regPassword, setRegPassword] = useState("");
  var [regWalletAdd, setRegWalletAdd] = useState("");

  const handleSubmit = () => {
    setIsLoading(true);

    let dataToSend = {
      username: loginValue,
      password: passwordValue,
    };

    fetch("http://3.227.99.109/api/login", {
      method: "POST",
      body: JSON.stringify(dataToSend),
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((response) => response.json())
      .then((responseJson) => {
        if (responseJson.status === "success") {
          // console.log(responseJson.message.wallet_address);
          setIsLoading(false);

          localStorage.setItem(
            "loggedUser",
            responseJson.message.wallet_address
          );

          props.onHide();
          window.location.reload();
        } else {
          setIsLoading(false);
          setError("Please Enter Valid Credentials");
          setTimeout(() => {
            setError("");
          }, 2000);
        }
      })
      .catch((error) => {
        setIsLoading(false);

        console.error(error);
      });
  };

  const registerHandler = () => {
    setIsLoading(true);

    var data = JSON.stringify({
      fullname: regFullName,
      username: regUserName,
      email: regEmail,
      password: regPassword,
      wallet_address: regWalletAdd,
      user_type: "Guest",
    });

    var config = {
      method: "post",
      url: "http://3.227.99.109/api/register_user",
      headers: {
        "Content-Type": "application/json",
      },
      data: data,
    };

    axios(config)
      .then(function (response) {
        setIsLoading(false);

        console.log(JSON.stringify(response.data));
        swal({
          title: "Registered Successfully!",
          icon: "success",
          button: "ok",
        });
        setRegFullName("");
        setRegUserName("");
        setRegEmail("");
        setRegPassword("");
        setRegWalletAdd("");
      })
      .catch(function (error) {
        setIsLoading(false);

        console.log(error);
      });
  };

  return (
    <Modal {...props} aria-labelledby="contained-modal-title-vcenter">
      <Modal.Body className="show-grid">
        <Container>
          <i
            className="fa fa-arrow-left"
            style={{ cursor: "pointer" }}
            onClick={props.onHide}
          ></i>

          <Row>
            <Col xs={12} md={4}>
              <Tabs
                value={activeTabId}
                onChange={(e, id) => setActiveTabId(id)}
                indicatorColor="primary"
                centered
              >
                <Tab label="Login" />
                <Tab label="New User" />
              </Tabs>
              {activeTabId === 0 && (
                <div className="signup-classic">
                  <TextField
                    id="email"
                    InputProps={{
                      classes: {
                        underline: classes.textFieldUnderline,
                        input: classes.textField,
                      },
                    }}
                    value={loginValue}
                    onChange={(e) => setLoginValue(e.target.value)}
                    margin="normal"
                    type="text"
                    fullWidth
                    label="User Name"
                    style={{ color: "#afb1be" }}
                    variant="filled"
                  />
                  <TextField
                    id="password"
                    InputProps={{
                      classes: {
                        underline: classes.textFieldUnderline,
                        input: classes.textField,
                      },
                    }}
                    value={passwordValue}
                    onChange={(e) => setPasswordValue(e.target.value)}
                    margin="normal"
                    type="password"
                    fullWidth
                    label="Password"
                    variant="filled"
                  />
                  {error === "Please Enter Valid Credentials" ? (
                    <p style={{ color: "red" }}>
                      Please Enter Valid Credentials
                    </p>
                  ) : null}
                  <div className={classes.formButtons}>
                    {isLoading ? (
                      <CircularProgress
                        size={26}
                        className={classes.loginLoader}
                      />
                    ) : (
                      <Button
                        disabled={
                          loginValue.length === 0 || passwordValue.length === 0
                        }
                        onClick={handleSubmit}
                        variant="contained"
                        size="large"
                        className={classes.createAccountButton}
                      >
                        Login
                      </Button>
                    )}
                  </div>
                </div>
              )}
              {activeTabId === 1 && (
                <div className="signup-classic">
                  <TextField
                    value={regFullName}
                    InputProps={{
                      classes: {
                        underline: classes.textFieldUnderline,
                        input: classes.textField,
                      },
                    }}
                    onChange={(e) => setRegFullName(e.target.value)}
                    margin="normal"
                    type="text"
                    fullWidth
                    id="standard-basic"
                    label="Full Name"
                    variant="filled"
                  />
                  <TextField
                    id="name"
                    InputProps={{
                      classes: {
                        underline: classes.textFieldUnderline,
                        input: classes.textField,
                      },
                    }}
                    value={regUserName}
                    onChange={(e) => setRegUserName(e.target.value)}
                    margin="normal"
                    type="text"
                    fullWidth
                    label="User Name (Minimum length 8)"
                    error={regUserName != "" && regUserName.length < 8}
                    variant="filled"
                  />
                  <TextField
                    id="email"
                    InputProps={{
                      classes: {
                        underline: classes.textFieldUnderline,
                        input: classes.textField,
                      },
                    }}
                    value={regEmail}
                    onChange={(e) => setRegEmail(e.target.value)}
                    margin="normal"
                    type="email"
                    fullWidth
                    label="Email Adress"
                    error={
                      regEmail &&
                      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(
                        regEmail
                      )
                    }
                    variant="filled"
                  />
                  <TextField
                    id="password"
                    InputProps={{
                      classes: {
                        underline: classes.textFieldUnderline,
                        input: classes.textField,
                      },
                    }}
                    value={regPassword}
                    onChange={(e) => setRegPassword(e.target.value)}
                    margin="normal"
                    type="password"
                    fullWidth
                    minLength={6}
                    label="Password (Minimum length 6)"
                    error={regPassword != "" && regPassword.length < 6}
                    variant="filled"
                  />
                  <TextField
                    id="name"
                    InputProps={{
                      classes: {
                        underline: classes.textFieldUnderline,
                        input: classes.textField,
                      },
                    }}
                    value={regWalletAdd}
                    onChange={(e) => setRegWalletAdd(e.target.value)}
                    margin="normal"
                    type="text"
                    fullWidth
                    label="Wallet Address"
                    variant="filled"
                  />
                  <div className={classes.creatingButtonContainer}>
                    {isLoading ? (
                      <CircularProgress size={26} />
                    ) : (
                      <Button
                        onClick={registerHandler}
                        disabled={
                          regFullName.length === 0 ||
                          regUserName.length < 8 ||
                          regEmail.length === 0 ||
                          regPassword.length < 6 ||
                          regWalletAdd.length === 0
                        }
                        size="large"
                        variant="contained"
                        fullWidth
                        className={classes.createAccountButton}
                      >
                        Create your account
                      </Button>
                    )}
                  </div>
                </div>
              )}
            </Col>
            <Col xs={12} md={8}>
              <img style={{ width: "100%" }} src={ModalBg} />
            </Col>
          </Row>
        </Container>
      </Modal.Body>
    </Modal>
  );
}

function App(props) {
  const [modalShow, setModalShow] = useState(false);

  return (
    <>
      <Button
        style={{ color: "black", backgroundColor: "white" }}
        onClick={() => setModalShow(true)}
      >
        {props.Name}
      </Button>

      <MydModalWithGrid show={modalShow} onHide={() => setModalShow(false)} />
    </>
  );
}

export default App;
