import React, { useEffect, useState } from "react";
import ReactPaginate from "react-paginate";
import ethlogo from "./ethlogo.png";

var axios = require("axios");

const Products = (props) => {
  const [pageNumber, setPageNumber] = useState(0);
  const [data, setData] = useState([]);

  console.log("props=>", props);

  useEffect(() => {
    setData(props.location.state.allData);
  }, []);

  // const data = props.history.location.state.allData;
  // console.log(data);

  const ItemsPerPage = 12;
  const pagesVisited = pageNumber * ItemsPerPage;

  const displayItems = data.slice(pagesVisited, pagesVisited + ItemsPerPage);

  console.log("displayItems=>", displayItems);

  const pageCount = Math.ceil(data.length / ItemsPerPage);

  console.log("pagecount=>", pageCount);

  const changePage = ({ selected }) => {
    setPageNumber(selected);
  };

  const handleClick = (e) => {
    localStorage.setItem("clickedProduct", e);
    props.history.push("/ProductDetails" + "?" + `${e}`);
  };

  return (
    <div>
      <div className="cardPortionBg "></div>
      <div className="content">
        <div className="section">
          <div>
            <h1>Colossalbit NFT List</h1>
            <p className="top-content">
              Buy, sell, and discover rare digital items
            </p>
          </div>
        </div>
      <div
        className="container"
        style={{ marginBottom: "170px" }}
      >
        
        <div className="row">
          {displayItems
            .filter(function (element) {
              return element.image_original_url !== null;
            })
            .filter(function (element) {
              return element.sell_orders !== null;
            })
            .map((element) => (
              <div className="col-sm-3">
                <div onClick={() => handleClick(element.id)}>
                  <div className="scroller--content">
                    <div>
                      <i className="fa fa-heart-o heart-icn" />
                    </div>
                    <img src={element.img} />
                    <p>{element.description}<br/> <img src={ethlogo} style={{width: "10px", height: "10px"}} />{element.price}</p>
                  </div>
                </div>
              </div>
            ))}
        </div>
      </div>

      <ReactPaginate
        previousLabel={"Previous"}
        nextLabel={"Next"}
        pageCount={pageCount}
        onPageChange={changePage}
        containerClassName={"paginationBttns"}
        previousLinkClassName={"previousBttn"}
        nextLinkClassName={"nextBttn"}
        disabledClassName={"paginationDisabled"}
        activeClassName={"paginationActive"}
      />
    </div>
    
    </div>
  );
};
export default Products;
